*{
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
}

html, body {
  margin: 0;
  height: 100%;
  width: 100vw;
}

#root {
  height: 100%;
  width: 100vw;
  margin: 0;
  padding: 0;
  background: #303030;
}